/* eslint-disable jsx-a11y/tabindex-no-positive */
import { useState, useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

// material-ui
import { Typography, List, ListItem, useMediaQuery, TextField, Divider } from '@mui/material';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import ProductCategory from './component/ProductCategory';
import Confirmation from './component/Confirmation';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //
let productModified = {};

const App = () => {
    //全局数据
    console.log('productEdit app running');
    let localization = useSelector((state) => state.localization); //多语言相关
    let globalization = useSelector((state) => state.globalization); //全局数据
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage
    let [loginData] = useState(globalization.baseData.loginData);
    let navigate = useNavigate(); //react-router-dom
    //编辑步骤提示 & 菜单折叠
    let [activeStep, setActiveStep] = useState(0);
    let [expanded, setExpanded] = useState({ panel1: true, panel2: false, panel3: false, panel4: false });
    //产品信息汇总
    let group_id = location.pathname.replace('/product/draft/', ''); //产品id
    let [productInfo, setProductInfo] = useState({}); //产品信息
    let [finalAttributeArr, setFinalAttributeArr] = useState([]); //在用的属性列表
    let [finalImageArr, setFinalImageArr] = useState([]); //在用的图片列表
    let [finalDescription, setFinalDescription] = useState(''); //在用的产品详情
    let [finalCatId, setFinalCatId] = useState(0); //当前产品类目id
    let [finalCustomArr, setFinalCustomArr] = useState([]); //当前产品定制信息
    let [categories, setCategories] = useState([]); //当前产品类目
    let categoryArr = []; //当前产品类目(暂存)
    let menuRef = useRef(null);
    let [menuState, setMenuState] = useState({
        menuLevel: undefined,
        menuValue: undefined,
        menuArr: [],
        anchorEl: null
    });

    useEffect(() => {
        if (!categories.length) return;
        let categoryResult = categories[categories.length - 1];
        let cat_id = categoryResult.category_id;
        setFinalCatId(cat_id);
        if (cat_id) {
            getCategoryAttributeInfo({ cat_id });
            productModified.category_id = cat_id;
        }
        //console.log(categories, categoryResult.category_id);
        return; //类目调整 返回最新的类目id
    }, [categories]);

    useEffect(() => {
        setLangPack(Languages[localization.locale]);
        return; //语言切换
    }, [localization.locale]);

    useEffect(() => {
        console.log('I want product drafting');
        if (!loginData) return navigate('/');
        getAllCategories(153710); //获取所有类目
        return () => {
            productModified = {};
            console.log('I leave product draft');
        }; //载入 & 清理
    }, []);

    //____________一些函数_______________________________________________
    async function makeProductDraft(obj) {
        let paramObj = ma.productAddDraftQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productAddDraftCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        if (!result.product_id) return queryResult(false, langPack.common.netFail);
        return console.log(status, '已存入草稿箱，暂不支持编辑，敬请期待...🤯');
    } //发布草稿

    function gotoEdit() {
        queryResult(true, langPack.common.success + '\n' + langPack.steps.redirecting);
        setTimeout(() => {
            navigate('/product/category_id=' + finalCatId);
        }, 1000);
    }

    async function getCategoryInfo(obj) {
        let paramObj = ma.productCategoryQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productCategoryCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        return result;
    } //获取类目信息

    async function getCategoryAttributeInfo(obj) {
        let paramObj = ma.productCategoryAttributeQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productCategoryAttributeCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        console.log(result);
        return;
    } //获取类目属性信息

    async function getAllCategories(category_id, category_level) {
        if (!category_level) {
            let categoryResult = await getCategoryInfo({ cat_id: category_id });
            if (!categoryResult) return;
            let { level, parent_ids } = categoryResult;
            if (!categoryArr.length) categoryArr = new Array(level); //初始化模式
            categoryArr[level] = categoryResult;
            if (level > 0 && parent_ids && parent_ids.number) {
                let cat_id = parent_ids.number[0];
                getAllCategories(cat_id);
            } else {
                setCategories(categoryArr);
            }
            return;
        }
        let categoryResult = categories[category_level - 1].child_ids.number.find((obj) => obj.category_id === category_id);
        if (!categoryResult) return;
        let { child_ids } = categoryResult;
        let newCategories = [...categories];
        newCategories[category_level] = categoryResult;
        if (category_level + 1 < newCategories.length) newCategories.length = category_level + 1;
        if (child_ids) {
            newCategories.push({ category_id: 0 });
            categoryArr = newCategories;
            showMenu(newCategories.length - 1);
        }
        return setCategories(newCategories); //修改类目模式
    }

    const showMenu = async (index) => {
        let latestCategories = categoryArr.length ? categoryArr : categories;
        let category_id = latestCategories[index].category_id;
        let childIdsArr = latestCategories[index - 1].child_ids.number;
        for (let cat_id of childIdsArr) {
            if (typeof cat_id === 'object') break;
            let categoryResult = await getCategoryInfo({ cat_id });
            childIdsArr[childIdsArr.indexOf(cat_id)] = categoryResult;
        }
        setMenuState({ menuLevel: index, menuValue: category_id, menuArr: childIdsArr, anchorEl: menuRef.current });
    }; //获取当前的类目id & 通过父级获取子类目选项
    const closeMenu = (category_id) => {
        let { menuLevel } = menuState;
        setMenuState({ menuArr: [], anchorEl: null });
        console.log(category_id);
        if (category_id !== undefined && category_id !== finalCatId) getAllCategories(category_id, menuLevel);
    }; //需要更新类目

    const modificationSubmission = async () => {
        return gotoEdit();
        let modifiedKeys = Object.keys(productModified);
        if (!modifiedKeys.length) return queryResult(false, langPack.steps.modificationNull);
        if (!categories.length || categories[categories.length - 1].category_id === 0 || categories[categories.length - 1].child_ids) {
            return queryResult(false, langPack.steps.categoryError);
        }
        let dataObj = {
            group_id,
            product_type: 'sourcing'
        }; //默认提交的数据
        for (let [k, v] of Object.entries(productModified)) {
            dataObj[k] = typeof v == 'object' ? JSON.stringify(v) : v;
        } //带上修改的数据
        console.log(dataObj);
        makeProductDraft(dataObj);
    };
    //____________Functions return JSX_______________________________________________

    return (
        <MainCard title="Product Draft --产品草稿">
            <Typography variant="h2">
                <span role="img" aria-label="hint">
                    👇
                </span>
                {langPack.form.category}
            </Typography>
            <br />
            <ProductCategory
                localization={localization}
                categories={categories}
                menuRef={menuRef}
                menuState={menuState}
                showMenu={showMenu}
                closeMenu={closeMenu}
            />
            <Divider sx={{ marginBottom: '50px' }} />
            <br />
            <Confirmation langPack={langPack} productModified={productModified} modificationSubmission={modificationSubmission} />
        </MainCard>
    );
};

export default App;
